html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  flex-direction: row;
}

body {
  flex: 0.5;
  flex-direction: column;
  align-items: stretch;
}

svg {
  flex: 0.5;
  box-shadow: 5px 5px;
  background: #DDD;
}